const defaultCircleProps = {
  r: 7,
  fill: "black",
}

const circleIsClickable = (clickables, { column, rowIndex }) => {
  return (clickables || []).find((x) => x === `${column}:${rowIndex}`)
}

const circleProps = ({ column, override, rowIndex }) => ({
  ...defaultCircleProps,
  key: `circle-${column}-${override.cy}`,
  id: `circle-${column}-${rowIndex}`,
  ...override,
})

const Column = ({
  index,
  cx,
  yValues,
  r = 7,
  fill,
  clickables = [],
  onClick,
}) =>
  yValues.map((cy, i) => {
    const defaultProps = { column: index, rowIndex: i }
    const props =
      typeof cy === "object"
        ? circleProps({ ...defaultProps, override: { cx, ...cy } })
        : circleProps({ ...defaultProps, override: { cx, cy, r, fill } })
    const clickable = circleIsClickable(clickables, defaultProps)
    props.onClick = clickable ? (x) => onClick(x) : null
    props.style = clickable ? { cursor: "pointer" } : null
    return <circle {...props} />
  })

const TargetModelArrow = ({ model, from, to }) => {
  const _model = Models[model]
  const lineY = (col, i) => {
    const yValue = _model.columns[col].yValues[i]
    return typeof yValue === "object" ? yValue.cy : yValue
  }
  const lineX = (col) => _model.columns[col].cx + defaultCircleProps.r
  const lineEndX = (col) => _model.columns[col].cx - defaultCircleProps.r
  return (
    <line
      x1={lineX(from[0])}
      y1={lineY(from[0], from[1])}
      x2={lineEndX(to[0])}
      y2={lineY(to[0], to[1])}
      stroke="#000"
      strokeWidth={0.7}
      markerEnd="url(#arrowhead)"
    />
  )
}

const TargetModelDiagram = ({
  model = "PuppiesToFood",
  style,
  id,
  additionalShapes = [],
  clickableCircles = [],
  onClickCircle = () => null,
}) => (
  <div style={style} id={id}>
    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 140 100">
      <defs>
        <marker
          id="arrowhead"
          markerWidth="6"
          markerHeight="6"
          refX="12"
          refY="3"
          orient="auto"
        >
          <polygon points="0 0, 6 3, 0 6" />
        </marker>
      </defs>
      {Models[model].columns.map((c, i) => (
        <Column
          key={`column-${i}`}
          index={i}
          clickables={clickableCircles}
          onClick={onClickCircle}
          {...c}
        />
      ))}

      {Models[model].arrows.map((a, i) => (
        <TargetModelArrow
          key={`target-model-arrow-${i}`}
          model={model}
          {...a}
        />
      ))}

      {additionalShapes}
    </svg>
  </div>
)

export default TargetModelDiagram
