import styled from "styled-components"
import FlowChart from "./FlowChart"
import TargetModelDiagram from "./TargetModel"

const ImageTitle = styled.h4`
  text-align: center;
  margin: 0 0 5px 0;
`

const TitledElement = ({
  title,
  children,
  bottom = false,
  bordered = false,
  style,
}) => (
  <div
    style={{
      display: "flex",
      flexDirection: "column",
      alignItems: "center",
      backgroundColor: bordered ? "white" : null,
      padding: bordered ? 4 : null,
      border: bordered ? "1px solid lightgray" : null,
      ...style,
    }}
  >
    {title && !bottom ? <ImageTitle>{title}</ImageTitle> : null}
    {children}
    {title && bottom ? <ImageTitle>{title}</ImageTitle> : null}
  </div>
)

const MiaFlowChart = ({ status, style }) => (
  <FlowChart
    name="mia-layout"
    size={{ height: 325, width: 500 }}
    style={style}
    elements={[
      {
        id: "mia-flow-horse",
        element: (
          <TitledElement>
            <img src="story-img/horse.png" alt="" />
          </TitledElement>
        ),
        style: { top: 140 },
        links: {
          "mia-flow-target-model": {
            startSocket: "top",
            endSocket: "left",
            size: 10,
            color: "midnightblue",
            endLabel: "Classify",
            path: "grid",
          },
        },
      },
      {
        id: "mia-flow-target-model",
        element: (
          <TitledElement
            title="Target Model"
            style={{ marginTop: 25 }}
            bordered={true}
          >
            <TargetModelDiagram style={{ width: 130 }} model="MIA" />
          </TitledElement>
        ),
        style: { left: 150, top: 10 },
        links: {
          "mia-flow-bar-chart": {
            path: "straight",
            color: "black",
          },
        },
      },
      {
        id: "mia-flow-bar-chart",
        element: (
          <TitledElement title="Yes or No?" bottom>
            <img src="story-img/bar-chart.png" alt="" />
          </TitledElement>
        ),
        style: { right: 0, top: 1 },
      },
      {
        id: "mia-flow-compliance-officer",
        element: (
          <TitledElement title="Compliance Officer" bottom bordered={true}>
            <img src="story-img/compliance-officer.png" alt="" />
          </TitledElement>
        ),
        style: { left: 150, bottom: 0 },
        links: {
          "mia-flow-horse": {
            startSocket: "left",
            endSocket: "bottom",
            size: 10,
            color: "midnightblue",
            path: "grid",
          },
          "mia-flow-bar-chart": {
            startSocket: "right",
            endSocket: "bottom",
            size: 10,
            color: "midnightblue",
            path: "grid",
          },
        },
      },
    ]}
    visible={status?.visible}
    startAnimating={status?.visible}
  />
)

export default MiaFlowChart
