import { useState } from "react"
import styled, { keyframes } from "styled-components"
import TargetModelDiagram from "./TargetModel"
import { Emoji } from "../game/Util"

const fadeIn = keyframes`
  from { opacity: 0; }
  to { opacity: 1; }
`

const Input = styled.div`
  height: 100px;
  width: 100px;
  background-color: gray;
  margin: 10px;
  margin-left: 35px;
  font-size: 1.2rem;
  &::before {
    content: "x\\208${({ index }) => index}";
    position: relative;
    left: -65%;
    display: flex;
    align-items: center;
    justify-content: center;
    height: inherit;
  }
`

const Inputs = ({ count, images }) => {
  const inputs = new Array(count).fill(0)
  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
      }}
    >
      {inputs.map((_, i) => (
        <Input
          key={`input-${i + 1}`}
          index={i + 1}
          id={`input-${i + 1}`}
          style={{
            backgroundImage: images[i] ? `url(story-img/${images[i]})` : null,
          }}
        />
      ))}
    </div>
  )
}

const StyledOutputDiagram = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  animation: ${fadeIn} 0.1s linear;
`

const Columns = {
  0: {
    middlex: 10,
  },
  1: {
    middlex: 50,
  },
  2: {
    middlex: 70,
  },
  3: {
    middlex: 89,
  },
}

const columnStyle = (heightOffset) => ({
  height: 10 + (heightOffset || 0),
  rx: 1,
  y: 40 - (heightOffset || 0),
})

const OutputDiagramColumn = ({ height, fill, x, width }) => (
  <rect fill={fill} style={columnStyle(height)} x={x} width={width} />
)

export const OutputDiagram = ({
  width = "100%",
  height = "100%",
  index,
  selectedColumn = 2,
}) => {
  const ellipseStyle = {
    stroke: "rgb(63,108,175)",
    strokeWidth: 4,
    fill: "none",
  }
  const columnHeight = (colIndex) =>
    selectedColumn === colIndex ? (index - 1) * 5 : null

  return (
    <StyledOutputDiagram
      style={{ width, height, position: "absolute" }}
      id={`output-${index}`}
    >
      <svg viewBox={`0 0 99 99`}>
        <OutputDiagramColumn
          height={columnHeight(0)}
          fill="rgb(176,0,4)"
          x={2}
          width={17}
        />
        <rect fill="black" x={21} y={44} height={2} width={2} />
        <rect fill="black" x={25} y={44} height={2} width={2} />
        <rect fill="black" x={29} y={44} height={2} width={2} />
        <OutputDiagramColumn
          height={columnHeight(1)}
          fill="black"
          x={42}
          width={16}
        />
        <OutputDiagramColumn
          height={columnHeight(2)}
          fill="rgb(255,226,129)"
          x={61}
          width={17}
        />
        <OutputDiagramColumn
          height={columnHeight(3)}
          fill="rgb(130,201,63)"
          x={80}
          width={17}
        />
        <ellipse
          cx={Columns[selectedColumn].middlex}
          cy="50"
          rx="15"
          ry="46"
          style={ellipseStyle}
        />
        <text
          x={Columns[selectedColumn].middlex}
          y="82"
          textAnchor="middle"
          style={{ font: "24px arial" }}
        >
          ?
        </text>
      </svg>
    </StyledOutputDiagram>
  )
}

export const Output = styled.div`
  position: relative;
  height: 100px;
  min-height: 100px;
  margin-top: -25px;
  padding: 0.25em;
  width: 120px;
  border: 3px solid #333;
  background-color: white;
  font-size: 1.3rem;
  &::before {
    position: relative;
    left: -80%;
    display: flex;
    align-items: center;
    justify-content: center;
    height: inherit;
  }
  &:nth-child(odd) {
    left: 150px;
    margin-left: 30px;
  }
  &:nth-child(even) {
    left: 35px;
  }
`

const Outputs = ({ count }) => {
  const inputs = new Array(count).fill(0)
  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
        maxHeight: 200,
        paddingTop: 25,
        justifyContent: "space-evenly",
      }}
    >
      {inputs.map((_, i) => (
        <Output index={i + 1} key={`output-${i + 1}`}>
          <OutputDiagram index={i + 1} />
        </Output>
      ))}
    </div>
  )
}

const ActionButton = styled.button`
  background-color: rgb(63, 108, 175);
  color: white;
  border: none;
  padding: 8px;
  border-radius: 5px;
  font-size: 16pt;
  outline: none;
  vertical-align: top;
  height: 42px;
  &:disabled {
    pointer-events: none;
    color: gray;
  }
  &:hover {
    text-shadow: 1px 1px 1px black;
  }
  &:nth-child(odd) {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    border-right: 2px solid gray;
  }
  &:nth-child(even) {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
  }
`

const Buttons = ({ onClick, actionDisabled }) => {
  return (
    <div style={{ textAlign: "center", marginBottom: 15 }}>
      <ActionButton onClick={() => onClick(1)} disabled={actionDisabled}>
        <Emoji symbol="0x203A" /> Step Inversion
      </ActionButton>
      <ActionButton onClick={() => onClick()} title="Reset">
        <Emoji symbol="0x21BB" />
      </ActionButton>
    </div>
  )
}

const InputOutput = ({ inputImages = [] }) => {
  const [inputCount, setInputCount] = useState(0)
  const updateInputCount = (delta) =>
    setInputCount((prev) => {
      if (delta === null || delta === undefined) setInputCount(1)
      else {
        const updated = prev + delta
        setInputCount(Math.min(Math.max(1, updated), 4))
      }
    })

  return (
    <div style={{ marginBottom: 300 }}>
      <Buttons onClick={updateInputCount} actionDisabled={inputCount === 4} />
      <div
        style={{
          display: "flex",
          justifyContent: "space-around",
          alignItems: "center",
          minWidth: 900,
          maxWidth: "90%",
        }}
      >
        <Inputs count={inputCount} images={inputImages} />
        <TargetModelDiagram
          style={{ width: 275 }}
          id="target-model"
          additionalShapes={[
            <circle
              key="circled-output-shape"
              cx="100"
              cy="50"
              r="12"
              stroke="rgb(63,108,175)"
              strokeWidth={2}
              fill="none"
            />,
          ]}
        />
        <Outputs count={inputCount} />
      </div>
    </div>
  )
}

export default InputOutput
