import { useState } from "react"
import ImageAnimation from "../ImageAnimation"
import Xarrow from "react-xarrows"
import TargetModelDiagram from "./TargetModel"
import { Output, OutputDiagram } from "./InputOutput"
import styled from "styled-components"

const lineProps = {
  color: "#FF4136",
  stroke: "#FF4136",
  strokeWidth: 2,
}

const StyledImageWrapper = styled.div`
  display: flex;
  align-items: center;
`

const ImageWrapper = ({ children, index }) => (
  <StyledImageWrapper id="redp-image">
    <div
      style={{
        position: "absolute",
        left: 10,
        ...lineProps,
      }}
    >
      x<sub>{index}</sub>
    </div>
    {children}
  </StyledImageWrapper>
)

const LineLabel = ({ index }) => (
  <div style={{ marginTop: 12, ...lineProps }}>
    (x<sub>{index}</sub>)
  </div>
)

const CircleArrowSourcePositions = {
  "3:0": {
    cx: 100,
    cy: 10,
    bottomness: -52,
  },
  "3:4": {
    cx: 100,
    cy: 50,
    bottomness: -2,
  },
  "3:5": {
    cx: 100,
    cy: 70,
    bottomness: 22,
  },
  "3:6": {
    cx: 100,
    cy: 90,
    bottomness: 48,
  },
  default: {
    cx: 100,
    cy: 50,
    bottomness: -2,
  },
}

const InputOutput = ({ index, onColumnIndexChanged }) => {
  const onClickCircle = (e) => {
    const [, x, y] = e.target.id.split("-")
    const key = `${x}:${y}`
    setCircleArrowSource(key in CircleArrowSourcePositions ? key : "default")
    const _y = parseInt(y)
    const column = _y !== 0 ? _y - 3 : _y // Skip the small dots
    setSelectedColumn(column)
    if (onColumnIndexChanged) onColumnIndexChanged(column)
  }
  const [circleArrowSource, setCircleArrowSource] = useState("3:4")
  const [selectedColumn, setSelectedColumn] = useState(1)
  const circleArrowSourceProps = () =>
    CircleArrowSourcePositions[circleArrowSource] ||
    CircleArrowSourcePositions.default
  return (
    <div
      style={{
        display: "flex",
        justifyContent: "space-around",
        alignItems: "center",
        minWidth: 350,
        marginTop: 15,
      }}
    >
      <TargetModelDiagram
        style={{ width: 175 }}
        id="redp-target-model"
        onClickCircle={onClickCircle}
        clickableCircles={["3:0", "3:4", "3:5", "3:6"]}
        additionalShapes={[
          <circle
            key="circled-output-shape"
            r="9"
            fill="none"
            {...circleArrowSourceProps()}
            {...lineProps}
          />,
        ]}
      />
      <Output
        id="redp-output"
        style={{ marginTop: -5, width: 100, marginRight: 10 }}
      >
        <OutputDiagram index={index} selectedColumn={selectedColumn} />
      </Output>
      <Xarrow
        start="redp-image"
        end="redp-target-model"
        startAnchor={{ position: "auto", offset: { rightness: -25 } }}
        {...lineProps}
      />
      <Xarrow
        start="redp-target-model"
        end="redp-output"
        startAnchor={{
          position: "auto",
          offset: {
            rightness: -37,
            ...circleArrowSourceProps(),
          },
        }}
        label={{
          end: <LineLabel index={index} />,
        }}
        {...lineProps}
      />
    </div>
  )
}

const imageNumbers = [1, 16, 32, 64, 128, 256]
const toImageSet = (_class) =>
  imageNumbers.map(
    (x) =>
      `story-img/match_game_cifar_robust_images_${_class}_threshold_${x}.png`
  )
const imageSets = ["deer", "plane", "horse", "boat"].map(toImageSet)

const Component = (props) => {
  const [selectedClass, setSelectedClass] = useState(1)
  const [selectedImage, setSelectedImage] = useState(0)
  return (
    <ImageAnimation
      images={imageSets[selectedClass]}
      info={
        <InputOutput
          index={selectedImage}
          onColumnIndexChanged={setSelectedClass}
        />
      }
      ImageWrapper={ImageWrapper}
      onChangeImage={setSelectedImage}
      delay={300}
      {...props}
    />
  )
}

export default Component
