const ScrollySectionWithBackgroundImage = ({
  width = "50%",
  minWidth = 700,
  img,
  title,
  children,
  status,
  sizePercent = 60,
  imageOpacity = 0.2,
}) => {
  const imageWidth =
    status && status.progress
      ? sizePercent + status.progress * (100 - sizePercent)
      : sizePercent
  return (
    <Centered>
      <div
        style={{
          width,
          minWidth,
          height: 400,
          position: "relative",
        }}
      >
        <img
          src={`story-img/${img}`}
          alt=""
          style={{
            position: "absolute",
            width: `${imageWidth}%`,
            height: "auto",
            opacity: imageOpacity,
            top: "50%",
            left: "50%",
            transform: " translate(-50%, -50%)",
          }}
        />
        <div
          style={{
            position: "absolute",
            width: "100%",
            top: "50%",
            left: "50%",
            transform: " translate(-50%, -50%)",
          }}
        >
          <ScrollyHeaderSection>{title}</ScrollyHeaderSection>
          <ScrollContainer>
            <ScrollyContentSection style={{}}>{children}</ScrollyContentSection>
          </ScrollContainer>
        </div>
      </div>
    </Centered>
  )
}

