const WeaknessWithAI = ({ status }) => {
  return (
    <div style={stepOpacityStyle(status)}>
      <ScrollySectionWithBackgroundImage
        title="The Weakness with AI"
        img="weakness.png"
        width="50%"
        status={status}
      >
        <p>
          The ability of deep neural network (DNN) machine learning (ML) models
          to achieve human or above-human task performance is a result of the
          availability of vast amounts of data. As organizations dedicate
          expensive resources to data acquisition, processing, labeling and
          storage and training of DNNs, the data must be treated as sensitive
          proprietary information. These facts are often expressed as data is
          the new oil and data is the new source code, respectively.
        </p>
        <p>
          However, recent demonstrations have shown how DNN characteristics and
          behaviors can be leveraged by malicious actors to make the DNN reveal,
          learn, or do the wrong thing. To see how DNNs can be exploited, we
          will first set the stage with an example of a successful machine
          learning application within the field of computer vision.
        </p>
      </ScrollySectionWithBackgroundImage>
    </div>
  )
}

