import React from "react"
import styled from "styled-components"
import { ResponsiveWaffle } from "@nivo/waffle"
import { ResponsivePie } from "@nivo/pie"
import { ResponsiveLine } from "@nivo/line"
import { isDef } from "../../util/misc"
import { RangeSlider } from "../game/Util"

export const ChartData = {
  line: (results, { groups }) => {
    const starter = groups.reduce(
      (all, g) => ({
        ...all,
        [g.label]: { id: g.label, color: g.color, data: [] },
      }),
      {}
    )
    const range = {
      x: {
        min: 99999,
        max: -99999,
      },
      y: {
        min: 99999,
        max: -99999,
      },
    }
    const data = Object.values(
      (results || []).reduce((all, item, i) => {
        const toData = (y) => ({
          x: i,
          y,
        })
        groups.forEach((g) => {
          const data = toData(g.fn(item))
          range.x.min = Math.min(range.x.min, data.x)
          range.y.min = Math.min(range.y.min, data.y)
          range.x.max = Math.max(range.x.min, data.x)
          range.y.max = Math.max(range.y.max, data.y)
          all[g.label].data.push(data)
        })
        return all
      }, starter)
    )
    return {
      range,
      data,
    }
  },
  rollup: (results, { title, type, groups, totalFn }, threshold) => {
    const result = results[threshold]
    return {
      groups,
      title,
      type,
      colors: groups.map((x) => x.color),
      data: [
        ...groups.map((g) => ({
          id: g.label,
          label: g.label,
          value: g.fn(result),
        })),
      ],
      total: totalFn ? totalFn(result) : 0,
    }
  },
}

const ChartContainer = styled.div`
  height: ${({ height = 150 }) => `${height}px`};
  width: ${({ width = 150 }) => `${width}px`};
  background-color: white;
  border: 1px solid lightgray;
  border-radius: 5px;
  margin: 5px;
`

const reducePropWithFn = (fn, propKey, data) =>
  data && data.length
    ? data.reduce(
        (cumulative, v) => fn(...v.data.map((d) => d[propKey]), cumulative),
        0
      )
    : 0

const Thresholds = {
  maxXValue: reducePropWithFn.bind(null, Math.max, "x"),
  maxYValue: reducePropWithFn.bind(null, Math.max, "y"),
  minXValue: reducePropWithFn.bind(null, Math.min, "x"),
  minYValue: reducePropWithFn.bind(null, Math.min, "y"),
}

const pointsAtThreshold = (series, threshold) =>
  series.map((s) => ({
    ...s.data[threshold].position,
    color: s.color,
    label: s.id,
  }))

const Threshold = ({
  data,
  dataRange,
  threshold,
  xScale,
  yScale,
  columnWidth,
  series,
}) => {
  if (!isDef(data) || !isDef(threshold) || !isDef(columnWidth)) return null
  const beyondYBuffer = 5
  const [columnX1, columnY1, columnX2, columnY2] = [
    xScale(threshold),
    yScale(-beyondYBuffer),
    xScale(threshold),
    yScale(Thresholds.maxYValue(data) + beyondYBuffer),
  ]

  const minXEdge = xScale(dataRange?.x?.min) - xScale(2)
  const maxXEdge = xScale(dataRange?.x?.max) + xScale(1)
  const linePadding = columnWidth / 2.0 + 1
  const columnPolyPoints = [
    `${columnX1 - linePadding},${columnY1}`,
    `${columnX1 - linePadding},${columnY2}`,
    `${columnX2 + linePadding},${columnY2}`,
    `${columnX2 + linePadding},${columnY1}`,
  ]
  const leftAreaPolyPoints = [
    `${minXEdge},${columnY1}`,
    `${minXEdge},${columnY2}`,
    `${columnX1 - linePadding},${columnY2}`,
    `${columnX1 - linePadding},${columnY1}`,
  ]
  const rightAreaPolyPoints = [
    `${columnX2 + linePadding},${columnY2}`,
    `${columnX2 + linePadding},${columnY1}`,
    `${maxXEdge},${columnY1}`,
    `${maxXEdge},${columnY2}`,
  ]
  const polyPoints = (points) => points.join(" ")

  const leftOfSliderLabel = "LESS"
  const rightOfSliderLabel = "MORE"
  const outsideAreaColor = "rgba(100,100,100,.2)"
  const sliderTopLabel = null

  const rightLabelX = maxXEdge - (maxXEdge - columnX2) / 2.0
  const leftLabelX = columnX1 / 2 + minXEdge / 2
  return isNaN(columnX1) ||
    isNaN(columnX2) ||
    isNaN(columnY1) ||
    isNaN(columnY2) ? null : (
    <>
      <>
        {leftOfSliderLabel && threshold > dataRange.x.min ? (
          <text
            x={leftLabelX}
            y={-7}
            textAnchor="middle"
            style={{ fontSize: ".7rem" }}
          >
            &#8592; {leftOfSliderLabel}
          </text>
        ) : null}
        {sliderTopLabel ? (
          <text
            x={columnX1}
            y={-7}
            textAnchor="middle"
            style={{ fontSize: ".63rem" }}
          >
            {sliderTopLabel}
          </text>
        ) : null}
        {rightOfSliderLabel && threshold < dataRange.x.max ? (
          <text
            x={rightLabelX}
            y={-7}
            textAnchor="middle"
            style={{ fontSize: ".7rem" }}
          >
            {rightOfSliderLabel} &#8594;
          </text>
        ) : null}
      </>
      <polygon
        points={polyPoints(columnPolyPoints)}
        fill="rgba(0,0,0,0)"
        stroke="rgba(20,20,20,1)"
        strokeWidth="1.5"
      />
      <polygon
        points={polyPoints(leftAreaPolyPoints)}
        fill={outsideAreaColor}
      />
      <polygon
        points={polyPoints(rightAreaPolyPoints)}
        fill={outsideAreaColor}
      />
      {pointsAtThreshold(series, threshold).map((p, i) => (
        <React.Fragment key={`highlighted-point-${p.label}`}>
          <circle cx={p.x} cy={p.y} r={6} fill={p.color} />
          <text
            x={p.x + 11}
            y={p.y}
            dominantBaseline={
              i % 2 === 0 ? "text-after-edge" : "text-before-edge"
            }
            style={{ fontSize: ".9rem" }}
          >
            {p.label}
          </text>
        </React.Fragment>
      ))}
    </>
  )
}

const Lines = ({ model, columnWidth = 15, threshold, left = 45 }) => {
  return model ? (
    <ResponsiveLine
      data={model.data}
      margin={{ top: 20, bottom: 30, right: 20, left }}
      colors={{ datum: "color" }}
      threshold={threshold}
      columnWidth={columnWidth}
      dataRange={model.range}
      enablePoints={false}
      enableSlices="x"
      axisLeft={{
        tickValues: 5,
        legend: "Model Accuracy",
        legendOffset: -37,
        legendPosition: "middle",
      }}
      yScale={{
        type: "linear",
        min: "auto",
        max: "auto",
      }}
      layers={[
        "grid",
        "markers",
        "axes",
        "lines",
        "points",
        Threshold,
        "slices",
        "crosshair",
      ]}
    />
  ) : null
}

const StyledChartTitle = styled.div`
  font-weight: bold;
  font-size: 0.8rem;
  text-align: center;
  margin: 5px 0;
`
const WaffleDot = ({
  size,
  x,
  y,
  color,
  fill,
  opacity,
  borderWidth,
  borderColor,
  onHover,
  onLeave,
}) => (
  <circle
    r={size / 2}
    cx={x + size / 2}
    cy={y + size / 2}
    fill={fill || color}
    strokeWidth={borderWidth}
    stroke={borderColor}
    opacity={opacity}
    onMouseEnter={onHover}
    onMouseMove={onHover}
    onMouseLeave={onLeave}
  />
)
const Waffle = ({ model }) => {
  if (!model.data) return null
  const total = 100 // Hard coded to get the desired dot mapping
  return (
    <>
      {model.title ? <StyledChartTitle>{model.title}</StyledChartTitle> : null}
      <ResponsiveWaffle
        data={model.data}
        total={total}
        rows={Math.sqrt(total)}
        columns={Math.sqrt(total)}
        colors={model.colors}
        margin={{ top: 0, left: 10, right: 10 }}
        cellComponent={WaffleDot}
        fillDirection="top"
        emptyColor="rgba(0,0,0,0)"
      />
    </>
  )
}

const PieCenter = ({
  dataWithArc,
  centerX,
  centerY,
  fn = () => 0,
  fontSize = "1.5rem",
}) => (
  <text
    x={centerX}
    y={centerY}
    textAnchor="middle"
    dominantBaseline="central"
    style={{
      fontSize,
    }}
  >
    {fn(dataWithArc)}
  </text>
)

// const PieCenterTotal = (props) => (
//   <PieCenter
//     {...props}
//     fn={(data) => data.reduce((all, x) => all + x.value, 0)}
//   />
// )

const PieCenterPercent = (props) => (
  <PieCenter
    {...props}
    fn={(data) => {
      const total = data.reduce((all, x) => all + x.value, 0)
      const percent = ((data[0].value / total) * 100).toFixed()
      return `${percent}%`
    }}
    fontSize="1rem"
  />
)

const Pie = ({ model, showTotal = true }) => {
  return (
    <>
      {model.title ? <StyledChartTitle>{model.title}</StyledChartTitle> : null}
      <ResponsivePie
        data={model.data}
        margin={{ top: 35, right: 120, bottom: 80, left: 120 }}
        colors={model.colors}
        innerRadius={0.5}
        enableRadialLabels={false}
        padAngle={0.5}
        // cornerRadius={2}
        layers={[
          "slices",
          "sliceLabels",
          "radialLabels",
          "legends",
          showTotal ? PieCenterPercent : null,
        ]}
        // sliceLabel={(x) => (x.value ? x.value : null)}
        sliceLabel={(x) => null}
        sliceLabelsTextColor="white"
      />
    </>
  )
}

const QuadChart = ({ model }) =>
  model.type === "pie" ? (
    <Pie model={model} />
  ) : model.type === "waffle" ? (
    <Waffle model={model} />
  ) : null

const LegendPart = ({ color, text, index }) => {
  const swatch = (
    <div
      style={{
        width: 14,
        height: 14,
        borderRadius: "50%",
        backgroundColor: color,
        margin: "0 5px",
      }}
    />
  )
  const colorPosition = index % 2 === 0 ? "right" : "left"
  return (
    <div
      style={{
        display: "flex",
        textAlign: colorPosition,
        justifyContent: colorPosition === "left" ? "flex-start" : "flex-end",
        padding: "1px 0",
      }}
    >
      {colorPosition === "left" ? swatch : null}
      <span style={{ fontSize: ".6rem" }}>{text}</span>
      {colorPosition === "right" ? swatch : null}
    </div>
  )
}

const Legend = ({ model }) => (
  <div
    style={{
      display: "grid",
      gridTemplateColumns: "50% 50%",
    }}
  >
    {model.map((x, i) => (
      <LegendPart key={`legend-part-${i}`} index={i} {...x} />
    ))}
  </div>
)

const FlexDiv = styled.div`
  display: flex;
`
const FlexColumns = styled(FlexDiv)`
  flex-direction: column;
`
const FlexRows = styled(FlexDiv)`
  flex-direction: row;
`
const ChartSet = ({
  height = 300,
  width = 600,
  threshold,
  models,
  sliderStep = 1,
  sliderLabel,
  setThreshold,
  legendModel,
}) => {
  const mainChartWidth = width / 2
  const smallChartHeight = height / 2 - 6
  const marginLeft = 45
  return (
    <FlexDiv>
      {models?.main?.range?.x?.max ? (
        <FlexColumns>
          <ChartContainer height={height} width={mainChartWidth}>
            <Lines
              model={models.main}
              threshold={threshold}
              left={marginLeft}
            />
          </ChartContainer>
          <RangeSlider
            label={sliderLabel}
            labelPosition="bottom"
            min={models?.main.range.x.min}
            max={models?.main.range.x.max}
            step={sliderStep}
            value={threshold}
            style={{
              outline: "none",
              width: mainChartWidth - marginLeft - 20,
              marginLeft: marginLeft + 6,
              marginTop: 4,
            }}
            handleStyle={{
              padding: 3,
              borderRadius: "0 0 50% 50%",
            }}
            onChange={(level) => setThreshold(level)}
          />
        </FlexColumns>
      ) : null}
      <FlexColumns>
        <FlexRows>
          <ChartContainer height={smallChartHeight}>
            <QuadChart model={models.quad1} />
          </ChartContainer>
          <ChartContainer height={smallChartHeight}>
            <QuadChart model={models.quad2} />
          </ChartContainer>
        </FlexRows>
        <FlexRows>
          <ChartContainer height={smallChartHeight}>
            <QuadChart model={models.quad3} />
          </ChartContainer>
          <ChartContainer height={smallChartHeight}>
            <QuadChart model={models.quad4} />
          </ChartContainer>
        </FlexRows>
        <Legend model={legendModel} />
      </FlexColumns>
    </FlexDiv>
  )
}

export default ChartSet
