import { useEffect, useState } from "react"
import LabelOnlyDistanceChartSet, {
  ChartData,
} from "./LabelOnlyDistanceChartSet"

const Colors = {
  red: "#FF4136",
  blue: "#0074D9",
  gray: "gray",
  test: "orange",
  train: "cornflowerblue",
}

const LegendModel = [
  { color: Colors.red, text: "TEST" },
  { color: Colors.blue, text: "TRAIN" },
]

const IN_RATE_DIVISOR = 50
const CORRECT_RATE_DIVISOR = 100

const correctRate = (x) =>
  (x?.test_results?.target_test_fgs_incorrect || 0) +
  (x?.train_results?.target_train_fgs_correct || 0)

const ChartModels = {
  main: {
    groups: [
      {
        label: "Train",
        fn: (x) => x.train_results.target_train_fgs_acc,
        color: Colors.blue,
      },
      {
        label: "Test",
        fn: (x) => x.test_results.target_test_fgs_acc,
        color: Colors.red,
      },
    ],
  },
  quad1: {
    title: "Attacked Correctly",
    type: "waffle",
    groups: [
      {
        label: "Test",
        fn: (x) => x?.test_results?.target_test_fgs_incorrect || 0,
        color: Colors.red,
      },
      {
        label: "Train",
        fn: (x) => x?.train_results?.target_train_fgs_correct || 0,
        color: Colors.blue,
      },
    ],
  },
  quad2: {
    title: "Attacked Incorrectly",
    type: "waffle",
    groups: [
      {
        label: "Train",
        fn: (x) => x?.train_results?.target_train_fgs_incorrect || 0,
        color: Colors.blue,
      },
      {
        label: "Test",
        fn: (x) => x?.test_results?.target_test_fgs_correct || 0,
        color: Colors.red,
      },
    ],
  },
  quad3: {
    title: "True IN Rate",
    type: "pie",
    groups: [
      {
        label: "IN",
        fn: (x) => x?.train_results?.target_train_fgs_correct || 0,
        color: Colors.blue,
      },
      {
        label: "OUT",
        fn: (x) =>
          IN_RATE_DIVISOR - (x?.train_results?.target_train_fgs_correct || 0),
        color: Colors.gray,
      },
    ],
  },
  quad4: {
    title: "Correct Rate",
    type: "pie",
    groups: [
      {
        label: "Correct",
        fn: (x) => correctRate(x),
        color: Colors.blue,
      },
      {
        label: "Incorrect",
        fn: (x) => CORRECT_RATE_DIVISOR - correctRate(x),
        color: Colors.gray,
      },
    ],
  },
}
const ExperimentLabelOnlyDisanceChartSet = ({
  nodeSize = 10,
  defaultNoise = 5,
  height = 300,
  width = 600,
  bottomLabel,
  sliderLabel,
}) => {
  const [rawData, setRawData] = useState([])
  const [models, setModels] = useState({
    main: { data: [] },
    quad1: { data: [], total: 0 },
    quad2: { data: [], total: 0 },
    quad3: { data: [], total: 0 },
    quad4: { data: [], total: 0 },
  })
  // const [dotPlotMaxX, setDotPlotMaxX] = useState(0)
  const [noise, setNoise] = useState(defaultNoise)

  useEffect(() => {
    const getRawData = async () => {
      const result = await fetch("./data/label-only-distance-data.json")
      const data = await result.json()
      const zeroResult = {
        train_results: {
          target_train_fgs_acc: data.target_train_acc,
          target_train_fgs_correct: data.target_train_correct,
          target_train_fgs_incorrect: data.target_train_incorrect,
        },
        test_results: {
          target_test_fgs_acc: data.target_test_acc,
          target_test_fgs_correct: data.target_test_correct,
          target_test_fgs_incorrect: data.target_test_incorrect,
        },
      }
      setRawData([zeroResult, ...data.results])
    }

    getRawData()
  }, [])

  useEffect(() => {
    setModels({
      main: ChartData.line(rawData, ChartModels.main),
      quad1: ChartData.rollup(rawData, ChartModels.quad1, defaultNoise),
      quad2: ChartData.rollup(rawData, ChartModels.quad2, defaultNoise),
      quad3: ChartData.rollup(rawData, ChartModels.quad3, defaultNoise),
      quad4: ChartData.rollup(rawData, ChartModels.quad4, defaultNoise),
    })

    // if (_dotPlotData.maxX) setDotPlotMaxX(_dotPlotData.maxX + 1)
  }, [rawData, defaultNoise])

  useEffect(() => {
    setModels({
      ...models,
      quad1: ChartData.rollup(rawData, ChartModels.quad1, noise),
      quad2: ChartData.rollup(rawData, ChartModels.quad2, noise),
      quad3: ChartData.rollup(rawData, ChartModels.quad3, noise),
      quad4: ChartData.rollup(rawData, ChartModels.quad4, noise),
    }) // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [noise])
  // return null
  return (
    <LabelOnlyDistanceChartSet
      models={models}
      sliderLabel={sliderLabel}
      threshold={noise}
      setThreshold={setNoise}
      legendModel={LegendModel}
    />
  )
}

export default ExperimentLabelOnlyDisanceChartSet
