import { useEffect, useCallback, useMemo, useState } from "react"
import styled from "styled-components"
import { shuffle, randomItem, isDef, filterObject } from "../../util/misc"
import { DndProvider, useDrag, useDrop } from "react-dnd"
import { HTML5Backend } from "react-dnd-html5-backend"
import Xarrow from "react-xarrows"
import { matchData } from "../../util/data"
import { GameTitle, WrongAnswerX } from "./Util"

const SIZES = {
  tile: {
    height: 100,
    width: 100,
  },
}

const DraggableImageBase = styled.div`
  height: ${SIZES.tile.height}px;
  width: ${SIZES.tile.width}px;
  border: 1px solid slategray;
  border-radius: 5px;
  background-image: ${({ src }) => `url(${src})`};
  background-size: cover;
`
const StyledDraggableImage = styled(DraggableImageBase)`
  border-color: black;
`
const DraggableImage = ({
  index,
  image,
  correctTargets,
  disabled,
  onGuess = () => null,
}) => {
  const draggableId = `draggable${index}`
  const [{ isDragging }, drag] = useDrag(() => ({
    type: "draggable",
    item: { name: image },
    end: (item, monitor) => {
      const dropResult = monitor.getDropResult()
      if (item && dropResult) {
        onGuess(draggableId, {
          guess: correctTargets.includes(dropResult.name),
          source: { id: draggableId, img: item.name },
          target: { id: dropResult.name, img: dropResult.name },
        })
      }
    },
    collect: (monitor) => ({
      isDragging: monitor.isDragging(),
    }),
  }))
  const opacity = isDragging ? 0.4 : 1
  return (
    <StyledDraggableImage
      id={draggableId}
      role="draggable"
      src={`match-img/${image}`}
      alt={image}
      ref={disabled ? null : drag}
      style={{ opacity }}
    />
  )
}

const DraggableContainer = styled.div`
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
`
const ClearImageSelectionButton = styled.button`
  position: absolute;
  cursor: pointer;
  top: -10px;
  right: -10px;
  height: 20px;
  width: 20px;
  border-radius: 50%;
  border: 1px solid #777;
  &::after {
    content: "x";
    text-align: center;
    margin: auto;
  }
`
const DraggableTargetDrop = styled(DraggableImageBase)`
  background-color: #ddd;
  border: 1px dashed black;
  position: relative;
`
const DraggableTarget = ({
  id,
  image,
  matches,
  onClearSelection,
  disabled,
}) => {
  const [{ canDrop, isOver }, drop] = useDrop(() => ({
    accept: "draggable",
    drop: () => ({ name: id }),
    collect: (monitor) => {
      let canDrop = true
      try {
        canDrop = monitor.canDrop()
      } catch {
        // https://github.com/react-dnd/react-dnd/issues/236
        // Unsure of a workaround at this point
        canDrop = true
      }
      return {
        isOver: monitor.isOver(),
        canDrop,
      }
    },
  }))
  const isActive = canDrop && isOver
  const from = Object.values(matches).find((x) => x.to.id === id)?.from
  const backgroundColor = from
    ? null
    : isActive
    ? "#aaa"
    : canDrop
    ? "#ddd"
    : "white"

  return (
    <div style={{ display: "flex", flexDirection: "row" }}>
      <DraggableTargetDrop
        id={id}
        ref={drop}
        className="drop-target"
        from={from}
        style={{
          backgroundColor,
          backgroundImage: from ? `url(match-img/${from.img})` : null,
        }}
      >
        {from && !disabled ? (
          <ClearImageSelectionButton onClick={() => onClearSelection(id)} />
        ) : null}
      </DraggableTargetDrop>
      <DraggableImageBase
        src={`match-img/${image}`}
        alt={image}
        style={{
          marginLeft: 10,
        }}
      />
    </div>
  )
}

const SideContainer = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  background-color: steelblue;
  padding: 25px;
  border-radius: 10px;
  border: 1px solid gray;
  margin: 20px 0;
`

const MiddleSection = styled.div`
  width: 500px;
`
const GameInformation = () => (
  <>
    <p>On the left are image represenations for class labels of a data set</p>
    <p>used to train a machine learning model.</p>
    <p>
      Drag each class prototype image on the left to their match on the right.
    </p>
    <p>
      <b> You have three strikes before you are out! </b>
    </p>
    <p></p>
  </>
)

const GameInfo = {
  learn: { title: "Learn to Match", matchCount: 3 },
  easy: { title: "Easy Match Game", matchCount: 5 },
  hard: { title: "Hard Match Game", matchCount: 3, choiceCount: 6 },
}

const Centered = styled.div`
  margin-top: 10px;
  text-align: center;
`

const ScoreBoard = ({ guesses }) => (
  <div style={{ textAlign: "center", height: 100, marginTop: 5 }}>
    {new Array(guesses.incorrect).fill().map((_, i) => (
      <WrongAnswerX key={`incorrect-x-${i}`} />
    ))}
  </div>
)

const RetryButton = styled.button`
  height: 50px;
  border: 3px solid #111;
  border-radius: 25px;
  font-size: 1.5rem;
  padding: 0 20px;
  font-family: Futura;
  background-color: ${({ won }) => (won ? "green" : "slategray")};
  color: white;
  cursor: pointer;
  outline: none;
  position: relative;
  z-index: 9999;
`

const everythingHasAGuess = () =>
  [...document.getElementsByClassName("drop-target")].reduce(
    (all, x) =>
      all && isDef(x.style.backgroundImage) && x.style.backgroundImage !== "",
    true
  )

const Options = ({ guesses, onGameOver, onRetry }) => {
  const [won, setWon] = useState(false)
  const [buttonText, setButtonText] = useState(null)
  useEffect(() => {
    const winner = () =>
      everythingHasAGuess() &&
      guesses.total > 0 &&
      Object.keys(guesses.correct).filter((x) => guesses.correct[x]).length >=
        guesses.total
    const loser = () => guesses.incorrect >= guesses.maxStrikes
    const won = winner()
    setWon(won)
    const text = loser() ? "Try again?" : won ? "You won! Play again?" : null
    setButtonText(text)
    if (text) onGameOver()
  }, [guesses, onGameOver])
  return buttonText ? (
    <Centered>
      <RetryButton onClick={onRetry} won={won}>
        {buttonText}
      </RetryButton>
    </Centered>
  ) : null
}

const toKey = (x, index) => `${x}_${index}`
const MatchLayout = ({ model, gameMode, onRetry, maxStrikes = 3 }) => {
  const [disabled, setDisabled] = useState(false)
  // console.log(JSON.stringify(model, null, 2))

  const onGameOver = () => setDisabled((_) => true)
  const _onRetry = () => {
    setGuesses(resetGuesses())
    setMatches({})
    onRetry()
  }
  const targetHasGuess = (target, matches) =>
    Object.values(matches).reduce(
      (all, x) => all || target.id === x.to.id,
      false
    )

  const targets = useMemo(() => model?.targets || [], [model])
  const sources = useMemo(() => model?.sources || [], [model])
  const [matches, setMatches] = useState({})
  const resetGuesses = useCallback(
    () => ({
      maxStrikes,
      total: targets.length,
      correct: {},
      incorrect: 0,
    }),
    [targets, maxStrikes]
  )

  useEffect(() => {
    setDisabled(false)
    setGuesses(resetGuesses())
  }, [model, resetGuesses])

  const [guesses, setGuesses] = useState(resetGuesses())
  const onGuess = (sourceId, { guess, source, target }) => {
    setGuesses((prev) => ({
      ...prev,
      lastGuess: {
        sourceId,
        guess,
        source,
        target,
      },
      correct:
        guess === true
          ? { ...prev.correct, [sourceId]: guess }
          : guess === null
          ? { ...prev.correct, [sourceId]: false }
          : prev.correct,
      incorrect:
        guess === false
          ? Math.min(prev.incorrect + 1, prev.total)
          : prev.incorrect,
    }))
  }

  useEffect(() => {
    setMatches((prev) => {
      if (!guesses || !guesses.lastGuess) return prev
      const { sourceId, guess, source, target } = guesses.lastGuess
      if (targetHasGuess(target, prev)) return prev
      return {
        ...prev,
        [sourceId]: {
          from: source,
          to: target,
          color: guess ? "green" : "red",
        },
      }
    })
  }, [guesses])

  const onClearGuess = (image) => {
    setMatches((prev) => {
      const match = Object.keys(prev).find((x) => prev[x].to.id === image)
      return filterObject(prev, (k) => k !== match)
    })
  }

  return (
    <DraggableContainer>
      <SideContainer>
        {sources.map((x, i) => (
          <DraggableImage
            key={toKey(x, i)}
            index={i}
            image={x}
            disabled={disabled}
            correctTargets={model.matches[x]}
            onGuess={onGuess}
          />
        ))}
      </SideContainer>
      <MiddleSection>
        <ScoreBoard guesses={guesses} />
        <GameTitle information={<GameInformation />}>
          {GameInfo[gameMode].title || "Match Game"}
        </GameTitle>
        <Options guesses={guesses} onRetry={_onRetry} onGameOver={onGameOver} />
      </MiddleSection>
      <SideContainer key={new Date().getTime()}>
        {targets.map((x, i) => (
          <DraggableTarget
            key={toKey(x, i)}
            id={x}
            image={x}
            disabled={disabled}
            matches={matches}
            onClearSelection={onClearGuess}
          />
        ))}
      </SideContainer>
      {Object.values(matches).map((x) => (
        <Xarrow
          key={`line-${x.from.id}-${x.to.id}`}
          start={`${x.from.id}`}
          end={`${x.to.id}`}
          color={x.color}
          path="straight"
          animateDrawing="200ms"
        />
      ))}
    </DraggableContainer>
  )
}

const Match = ({
  gameMode = "easy",
  matchCount = GameInfo[gameMode].matchCount,
  choiceCount = GameInfo[gameMode].choiceCount || GameInfo[gameMode].matchCount,
}) => {
  const [retries, setRetries] = useState(0)
  const [model, setModel] = useState({})
  const [allImages, setAllImages] = useState({})
  const resetModel = useCallback(() => {
    const targets = shuffle(Object.keys(allImages)).slice(0, matchCount)
    const findMatch = (key) =>
      allImages[key].match[gameMode] && allImages[key].match[gameMode].length
        ? randomItem(allImages[key].match[gameMode])
        : key

    const allGameTypeMatches = () =>
      Object.keys(allImages)
        .map((x) => allImages[x].match[gameMode])
        .flat()

    const extraChoices = (initialChoices) => {
      const extraCount = choiceCount - matchCount
      return extraCount > 0
        ? shuffle(
            allGameTypeMatches().filter((x) => !initialChoices.includes(x))
          ).slice(0, extraCount)
        : []
    }
    const findTargets = (source) =>
      Object.keys(allImages).filter((key) =>
        (allImages[key].match[gameMode] || [key]).includes(source)
      )

    const initialMatches = targets.map(findMatch)
    const sources = shuffle([
      ...initialMatches,
      ...extraChoices(initialMatches),
    ])
    const matches = sources.reduce(
      (all, x) => ({ ...all, [x]: findTargets(x) }),
      {}
    )
    return {
      attempt: retries,
      targets,
      sources,
      matches,
    }
  }, [allImages, matchCount, retries, gameMode, choiceCount])

  useEffect(() => {
    const getMatchData = async () => {
      const _matchData = await matchData()
      setAllImages(_matchData)
    }

    getMatchData() // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  useEffect(() => {
    setModel(resetModel())
  }, [retries, resetModel])

  if (matchCount > choiceCount) {
    return <div>choiceCount must be the same or more than matchCount</div>
  }

  const retryGame = () => setRetries((prev) => prev + 1)
  return (
    <DndProvider backend={HTML5Backend}>
      <MatchLayout model={model} gameMode={gameMode} onRetry={retryGame} />
    </DndProvider>
  )
}

export default Match
